import React, { useContext, useState, useEffect, useCallback } from 'react';
import { styled } from '@mui/system';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import AuthContext from '../../contexts/AuthContext';
import { Button, TextField, Link, Grid, Box } from '@mui/material';

const CheckDetails = styled('div')((props) => ({
	fontSize: '12px',
	marginLeft: '5%',
	color: props.isOk ? 'black' : 'red',
}));

const Form = () => {
	const { authActions } = useContext(AuthContext);
	const navigate = useNavigate();

	const [id, setId] = useState('');
	const [password, setPassword] = useState('');
	const [nickname, setNickname] = useState('');

	const [isIdUnique, setIsIdUnique] = useState(true);
	const [isPwOk, setIsPwOk] = useState(false);
	const [isNicknameUnique, setIsNicknameUnique] = useState(true);

	const onChangeId = useCallback((e) => {
		setId(e.target.value);
	}, []);
	const onChangePassword = useCallback((e) => {
		setPassword(e.target.value);
	}, []);
	const onChangeNickname = useCallback((e) => {
		setNickname(e.target.value);
	}, []);

	//ID  
	useEffect(() => {
		axios
			.post(`${process.env.REACT_APP_SERVER}/auth/id`, {
				id: id,
			})
			.then(() => {
				setIsIdUnique(true);
			})
			.catch((err) => {
				setIsIdUnique(false);
			});
	}, [id]);

	// 
	useEffect(() => {
		const regPassword = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{5,}$/;
		if (regPassword.test(password)) setIsPwOk(true);
		else setIsPwOk(false);
	}, [password]);

	//  
	useEffect(() => {
		axios
			.post(`${process.env.REACT_APP_SERVER}/auth/nickname`, {
				nickname: nickname,
			})
			.then(() => {
				setIsNicknameUnique(true);
			})
			.catch((err) => {
				setIsNicknameUnique(false);
			});
	}, [nickname]);

	// 
	const onSubmit = useCallback(async (e) => {
		e.preventDefault();
		if (!isIdUnique) alert('  .');
		else if (!isPwOk) alert(' .');
		else if (id === '' || nickname === '') alert('  .');
		else {
			await axios
				.post(
					`${process.env.REACT_APP_SERVER}/auth/join`,
					{
						id: id,
						password: password,
						nickname: nickname,
					},
					{
						withCredentials: true,
						credentials: 'include',
					}
				)
				.then(async (res) => {
					await authActions.setIsAuthed(true);
					await authActions.setUser({
						id: res.data.user.id,
						nickname: res.data.user.nickname,
					});
					navigate('/');
				})
				.catch((err) => {
					alert(' .  .');
				});
		}
	}, [isIdUnique, isPwOk, id, nickname]);

	return (
		<Box component="form" noValidate onSubmit={onSubmit} sx={{ mt: 3 }}>
			<Grid container spacing={2}>
				<Grid item xs={12}>
					<TextField
						required
						fullWidth
						label=" "
						onChange={onChangeId}
					/>
				</Grid>
				<CheckDetails isOk={isIdUnique}>
					{isIdUnique ? '  .' : '  .'}
				</CheckDetails>
				<Grid item xs={12}>
					<TextField
						required
						fullWidth
						label=" "
						type="password"
						onChange={onChangePassword}
					/>
				</Grid>
				<CheckDetails isOk={isPwOk}>
					{isPwOk
						? '  .'
						: '5 ,     .'}
				</CheckDetails>
				<Grid item xs={12}>
					<TextField
						required
						fullWidth
						label=" "
						onChange={onChangeNickname}
					/>
				</Grid>
				<CheckDetails isOk={isNicknameUnique}>
					{isNicknameUnique ? '  .' : '  .'}
				</CheckDetails>
			</Grid>
			<Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
				
			</Button>
			<Grid container>
				<Grid item>
					<Link onClick={() => navigate('/login')} variant="body2">
						  ?
					</Link>
				</Grid>
			</Grid>
		</Box>
	);
};

export default Form;