import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Alert, Button } from '@mui/material';

const NotAuthed = () => {
	const navigate = useNavigate();
	const goHome = () => {
		navigate('/');
	};
	
	return (
		<>
			<Alert severity="error">  .</Alert>
			<Button
				onClick={goHome}
				fullWidth
				variant="contained"
				color="error"
			>
				 
			</Button>
		</>
	);
};

export default NotAuthed;